import Vue from 'vue'
import VueRouter from 'vue-router'
import HomePage from '../views/HomePage.vue'
import login from '../views/Login.vue'
import Device from '../views/Device.vue'
import DashboardDevice from '../views/DashboardDevice'

Vue.use(VueRouter)

const routes = [
  { path: '/login', component: login },
  {
    path: '/HomePage', name: 'HomePage', component: HomePage, 
      children:[
        { path: '/HomePage/DashboardDevice', component:DashboardDevice,},
        { path: '/HomePage/DashboardDevice/Device', component: Device },
      ]
  
        // children:[{path: '/HomePage/DashboardDevice/DevicePlug', component: DevicePlug }]  },]
  },
  { path: '/*', redirect: '/login' },
]
const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
