<template>
    <div>
      <a-modal v-model="Dialoglogout" style="top: 20px;">
      <div >Do you Want to Logout?</div>
      <template slot="footer">
        <a-button key="back" @click="handleCancel">Cancel</a-button>
        <a-button key="submit" type="danger" :loading="loadinglogout" @click="handleOk">submit</a-button>
      </template>
    </a-modal>
    </div>    

</template>

<script>
export default {
  data() {
    return {
      loadinglogout: false,
      Dialoglogout: false,
    };
  },
  methods: {
    open() {
      this.Dialoglogout = true;
    },
    handleOk() {
      this.$router.push("/login");
    },
    handleCancel() {
      this.Dialoglogout = false;
    }
  }
};
</script>
<style >
.color_blue {
  color: #40c4ff;
}
</style>





