    <v-row align="center" justify="center" class="ml-2 mr-5">
      <v-col class="mt-0" cols="6" style="text-align: -webkit-center;" height="250px">
      </v-col>
      <v-col cols="6" sm="6" md="6" height="400px">
        <v-card >
         <v-card-text>

         </v-card-text>
        </v-card>
      </v-col>
