.v-progress-circular {
  margin: 1rem;
}
.v-sheet--offset {
  top: -24px;
  position: relative;
}
.truncate {
  max-width: 200px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>