<template>
<!-- <body style="background-color: lightgrey;"> -->
<body style="background-color: lightgrey;">
  <v-hover v-slot:default="{ hover }" open-delay="200">
    <v-card
      class="mx-auto"
      :elevation="hover ? 16 : 2"
      max-width="600"
      max-height="700px"
      style="margin-top: 7%;"
      outlined
      raised
    >
      <v-row class="mt-12" align="center" justify="center">
        <v-col cols="12" align="center" justify="center">
          <!-- desktop -->
          <v-img
            class="white--text align-end animated fadeInUp slow hidden-sm-and-down"
            width="48%"
            src="../assets/img/engineering.png"
          ></v-img>
          <!-- mobile-->
          <v-img
            class="white--text align-end animated fadeInUp slow hidden-md-and-up mt-5"
            width="55%"
            src="../assets/img/engineering.png"
          ></v-img>
        </v-col>
        <v-col class="animated fadeInUp mt-8" cols="12" align="center" justify="center">
          <v-col class="mt-5" cols="8" sm="9">
            <v-text-field
              color="red darken-1 red--text"
              align-center
              v-model="username"
              height="32"
              prepend-inner-icon="mdi-account-circle"
              label="username"
              single-line
              solo
            ></v-text-field>
            <v-text-field
              color="red darken-1 red--text"
              solo
              v-model="password"
              prepend-inner-icon="mdi-lock"
              :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
              :type="show1 ? 'text' : 'password'"
              name="input-10-1"
              label="password"
              @click:append="show1 = !show1"
            ></v-text-field>
            <v-btn
              v-on:click="validate_login()"
              :loading="loading"
              block
              color="white--text"
              dark
              large
            >Login</v-btn>
          </v-col>
        </v-col>
      </v-row>
      <v-container>
        <v-row cols="9" style="margin-left: 8%;">
          <v-col cols="5" sm="5"></v-col>

          <v-col cols="4" sm="4" style="margin-left: 25%;"></v-col>
        </v-row>
      </v-container>

      <!-- <v-container>
                   <v-row cols="9" style="margin-left: 8%;">
                        <v-col cols="5" sm="5">
                        <v-btn class="text-uppercase" text color="grey" @click="Open_ModelForgetPassword()">Forget password ?</v-btn>
                    </v-col>
    
                        <v-col cols="4" sm="4" style="margin-left: 25%;">
                        <v-btn class="text-uppercase" text color="grey" @click="Open_ModelSignUP()">Sign up</v-btn>
                    </v-col>
                   </v-row>
      </v-container>-->
    </v-card>
  </v-hover>
  <!-- <ModelForgetPassword ref="ModelForgetPassword"></ModelForgetPassword>
  <ModelSignUP  ref="ModelSignUP"></ModelSignUP>-->
  <!-- loading -->
  <v-dialog
    v-model="loadingDialog"
    fullscreen
    hide-overlay
    persistent
    transition="dialog-transition"
  >
    <v-card
      class="pa-0 ma-0 -webkit-full-screen"
      max-height="full screen"
      max-width="auto"
      width="100%"
      style
      height="100%"
    >
      <v-row align="center" justify="center" width="100%">
        <v-col align="center" justify="center" style="margin-top: 15%;">
          <!-- desktop -->
          <v-img
            class="white--text align-end hidden-sm-and-down"
            width="25%"
            src="../assets/img/engineering.png"
          ></v-img>
          <!-- mobile-->
          <v-img
            class="white--text align-end hidden-md-and-up mt-5"
            width="15%"
            src="../assets/img/engineering.png"
          ></v-img>
        </v-col>
      </v-row>
      <br />
      <br />
      <v-row align="center" justify="center" width="100%">
        <v-col cols="4">
          <br />

          <v-progress-linear color="black" indeterminate reverse></v-progress-linear>

          <br />
        </v-col>
      </v-row>
    </v-card>
  </v-dialog>
</body>
</template>

<script>
// import ModelForgetPassword from '@/components/Login/ModelForgetPassword.vue'
// import ModelSignUP from '@/components/Login/ModelSignUP.vue'
import Swal from "sweetalert2";
export default {
  name: "Login",
  components: {
    // ModelForgetPassword,
    // ModelSignUP
  },
  data() {
    return {
      show1: false,
      loading: false,
      username: undefined,
      password: undefined,
      loadingDialog: false
    };
  },
  watch: {},
   created() {
    // this.userId = this.$cookies.get('userinformation')
    // this.ChekAuth()
  },
  mounted() {},
  methods: {
    Open_ModelForgetPassword() {
      this.$refs.ModelForgetPassword.open();
    },
    Open_ModelSignUP() {
      this.$refs.ModelSignUP.open();
    },
    open_dialogloading() {
      this.$refs.loadingDialog.open();
    },
    validate_login() {
      if (this.username === undefined || this.password === undefined) {
        this.loading = true;
        this.loading = false;
        Swal.fire({
          icon: "error",
          title: "Oops...",
          text: "Something went wrong!",
          footer: "<a>Please check username and password</a>"
        });
      } 
      else if(this.username != 'super' || this.password != 'administrator'){
            Swal.fire({
          icon: "error",
          title: "Oops...",
          text: "Something went wrong!",
          footer: "<a>Please check username and password</a>"
        });
      }
      else {
        this.button_login();
      }
    },
    //   ChekAuth() {
    //   if (this.$cookies.isKey('usertype')) {
    //     this.toPage('/login')  
    //   }
    // },
    // toPage(url) {
    //   this.$router.push(url)
    // },
    button_login() {
      this.loadingDialog = true;

      this.axios
        .post(this.$store.state.url_axios + "api/v1/users/login", {
          username: this.username,
          password: this.password
        })
        .then(
          response => {
            console.log(response);
            var result = response.data;
          
            if (result["message"] === "success") {
              localStorage.token = result["result"].access_token;
              localStorage.refresh_token = result["result"].refresh_token;

              // var SendPermission = result.permission;
              // var SendUserId = result.userid;
              // var SendUserFullname = result.fullName;
               this.loadingDialog = false;
              // var date_login = result.date_login;
              // console.log('ddddddddddddd',localStorage.token)
              // console.log(SendPermission)
              // console.log(SendUserId)
              // console.log(date_login)
              // this.$cookies.set("date_login", date_login, null, "/",'http://localhost/');

              // this.$cookies.set(
              //   "userinformation",
              //   SendUserId,
              //   null,
              //   "/",
              //   "http://localhost/"
              // );
              // this.$cookies.set(
              //   "usertype",
              //   SendPermission,
              //   null,
              //   "/",
              //   "http://localhost/"
              // );
              // this.$cookies.set(
              //   "userfullname",
              //   SendUserFullname,
              //   null,
              //   "/",
              //   "http://localhost/"
              // );
              // this.loadingDialog = true;
              this.$router.push("/HomePage/DashboardDevice");
            } else {
              this.$swal("กรุณาตรวจสอบ Username และ Password", "", "error");
              this.loading = false;
            }
          },
           response => {
                if (response.message === 'Request failed with status code 400') {
                  this.loading = false 
                  this.$swal('กรุณากรอกข้อมูลให้ครบถ้วน', '', 'warning')
                } else if (
                  response.message === 'Request failed with status code 500'
                ) {
                  this.loading = false 
                  this.$swal('เกิดข้อผิดพลาด กรุณาตรวจสอบการเชื่อมต่อ', '', 'error')
                } else if (
                  response.message === 'Request failed with status code 401'
                ) {
                  this.loading = false 
                  this.toPage('logout')
                } else if (
                  response.message === 'Request failed with status code 422'
                ) {
                  this.loading = false 
                  this.toPage('logout')
                } else if(
                  response.message === 'Network Error'  
                ){
                  this.loading = false 
                  this.$swal('กรุณาตรวจสอบการเชื่อมต่อ', '', 'warning')
                }
                  this.loading = false 
                // console.log(response.message)
        }
        ).catch(error => {
       console.log(error.response.data.error)
      });
    }
  }
};
</script>

<style scoped>
/* .backgroundimage {
    background-image: url(../assets/img/background-login.png);
} */

.yourElement {
  animation-duration: 3s;
  animation-delay: 2s;
  animation-iteration-count: infinite;
}
.img {
  max-width: 100%;
  height: auto;
  background-size: auto;
}
body {
  overflow: hidden;
  /* background-image: url('../assets/img/background.png'); */
  background-color: #cccccc;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  background-position: center;
}
</style>
