<script>
import ModelCreateDevice from "@/components/ModelCreateDevice.vue";
// import footerHomepage from '@/components/FooterHomepage'
import Swal from "sweetalert2";
export default {
  name: "DashboardDevice",
  components: {
    ModelCreateDevice
    //  footerHomepage
  },
  data() {
    return {
      alert: false,
      Index: "",
      Name: "",
      fab: false,
      loading: false,
      array_getdata: [],
      mode: "create",
      create: "",
      loadingdelete: false,
      color_delete: "",
      show: false,
      show1: false,
      show2: false,
      transition: "slide-y-reverse-transition",
      array_devicedata: [],
    };
  },
  created() {
    this.getdata_Listdevice();
  },
  methods: {
    Open_device(items) {
      
      var arrayDevice = this.array_devicedata;
      // console.log(this.typeDevice);
      var Device = {
        Index: arrayDevice.length + 1,
        machineID: items.machineID,
        machineName: items.machineName,
        department: items.department,
      };
      // console.log(Device)
      //  console.log('arrayDevice',arrayDevice)
      arrayDevice.push(Device);
      window.localStorage.setItem("arrayDevice", JSON.stringify(arrayDevice));

      this.$router.push("/HomePage/DashboardDevice/Device");
    },
    Open_delete_Device() {
      Swal.fire({
        title: "Are you sure?",
        text: "You won't be able to revert this!",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, delete it!"
      }).then(result => {
        if (result.value) {
          Swal.fire("Deleted!", "Your file has been deleted.", "success");
        }
      });
    },
    btn_create() {
      this.$refs.ModelCreateDevice.open();
    },
    btn_delete() {
      this.mode = "delete";
      console.log(this.mode);

      this.snackbar_delete = true;
      Swal.fire({
        imageUrl:
          "https://i.pinimg.com/originals/ff/fa/9b/fffa9b880767231e0d965f4fc8651dc2.gif",
        imageWidth: 200,
        imageHeight: 180,
        imageAlt: "Custom image",
        // icon: 'warning',
        title: "This is a Delete Mode !!!",
        showConfirmButton: false,
        confirmButtonColor: "grey",
        timer: 3000
      });
    },
    exit_mode_delete() {
      this.mode = "create";
      this.create = "";
    },
    getdata_Listdevice() {
      this.loading = true;
      this.axios({
        method: "GET",
        url: this.$store.state.url_axios + "api/v1/meters",
        headers: { Authorization: "Bearer " + localStorage.token }
      }).then(
        response => {
          this.loading = false;
          var result = response.data;
          console.log(result);

          if (result.len === 0) {
            this.$swal({ title: "ไม่มีข้อมูล", type: "info" });

            this.array_getdata = result["result"];
            this.loading = false;
          } else {
            this.array_getdata = result["result"];
            this.loading = false;

            console.log(this.array_getdata);
          }
        },
        response => {
          if (response.message === "Request failed with status code 400") {
            this.$swal("กรุณากรอกข้อมูลให้ครบถ้วน", "", "warning");
          } else if (
            response.message === "Request failed with status code 500"
          ) {
            this.$swal("เกิดข้อผิดพลาด กรุณาตรวจสอบการเชื่อมต่อ", "", "error");
          } else if (
            response.message === "Request failed with status code 401"
          ) {
            this.toPage("logout");
          } else if (
            response.message === "Request failed with status code 422"
          ) {
            this.toPage("logout");
          } else if (response.message === "Network Error") {
            this.$swal("กรุณาตรวจสอบการเชื่อมต่อ", "", "warning");
          }
          console.log(response.message);
        }
      );
    }
  }
};
</script>

