<template>
  <v-dialog
    v-model="ModelCreateDevice"
    max-width="50%"
    overlay-opacity="0.9"
    transition="dialog-bottom-transition"
  >
    <v-card class align="center">
      <v-card-text style="text-align: center;"></v-card-text>
      <v-card-text style="text-align: center;">
        <p class="display-1 font-weight-bold black--text">Create Device</p>
      </v-card-text>
      <v-divider class="mx-5" color="#801d27"></v-divider>
      <v-card-text>
        <v-container>
          <v-form>
            <v-row class="ml-0" align="center" justify="center">
              <v-col cols="2" align="left">
                <p class="font-weight-bold">Meter id:</p>
              </v-col>
              <v-col cols="8" align="left">
                <v-text-field
                  color="red darken-4"
                  label="mm_1910"
                  v-model="meterid"
                  type="text"
                  dense
                  solo
                  filled
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row class="ml-0" align="center" justify="center">
              <v-col cols="2" align="left">
                <p class="font-weight-bold">Meter name:</p>
              </v-col>
              <v-col cols="8" align="left">
                <v-text-field
                  color="red darken-4"
                  label="motor"
                  v-model="metername"
                  type="text"
                  dense
                  solo
                  filled
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row class="ml-0" align="center" justify="center">
              <v-col cols="2" align="left">
                <p class="font-weight-bold">Department:</p>
              </v-col>
              <v-col cols="8" align="left">
                <v-text-field
                  color="red darken-4"
                  label="R&D"
                  v-model="department"
                  type="text"
                  dense
                  solo
                  filled
                ></v-text-field>
              </v-col>
            </v-row>

            <div class="text-center">
              <v-row>
                <v-col>
                  <v-btn block color="white" @click="Cancel()">Cancel</v-btn>
                </v-col>
                <v-col>
                  <v-btn block color="black" dark @click="validate_before_submit()">Submit</v-btn>
                </v-col>
              </v-row>
            </div>
          </v-form>
        </v-container>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import Swal from "sweetalert2";
export default {
  data() {
    return {
      loading: false,
      meterid: undefined,
      metername: undefined,
      department: undefined,
      ModelCreateDevice: false
    };
  },
  created() {},
  watch: {},
  methods: {
    open() {
      this.ModelCreateDevice = true;
    },
    Cancel() {
      this.ModelCreateDevice = false;
    },
    validate_before_submit() {
      if (
        this.meterid === undefined ||
        this.metername === undefined ||
        this.department === undefined
      ) {
        Swal.fire({
          icon: "error",
          title: "Oops...",
          text: "Something went wrong!"
        });
      } else {
        this.Submit();
      }
    },
    Submit() {
       var source = {
         user_account: "super",
         meter_id:this.meterid,
         meter_name:this.metername,
         department: this.department
       } 
       console.log(source);
      this.axios({
        method: "POST",
        url: this.$store.state.url_axios + "/api/v1/meters",
        data: source,
        headers: { Authorization: "Bearer " + localStorage.token }
      }).then(response => {
        console.log(response.data);
        // this.resultcheck = response.data;
        // this.result = response.data.result;
       
        if (this.resultcheck["message"] === "success") {
          Swal.fire({
            icon: "success",
            title: "Create device success !!",
            showConfirmButton: false,
            timer: 1500
          });
        } else {
          Swal.fire({
            icon: "error",
            title: "Oops...",
            text: "Something went wrong!",
          });
        }
      });
    }
  }
};
</script>
<style >
.color_blue {
  color: #40c4ff;
}
</style>





