<template>
  <div id="chart">
    <v-row align="center" justify="center" class="ml-0 mr-0">
      <v-col cols="4" style="text-align: -webkit-center;">
        <v-card class="center" height="265px">
          <div id="chart">
            <apexchart type="donut" :options="chartOptionsPower" :series="seriesPower"></apexchart>
          </div>
        </v-card>
      </v-col>

      <v-col cols="4" style="text-align: -webkit-center;">
        <v-card class="center" height="265px">
          <div id="chart">
            <apexchart type="donut" :options="chartOptionsFrequency" :series="seriesFrequency"></apexchart>
          </div>
        </v-card>
      </v-col>

      <v-col cols="4" style="text-align: -webkit-center;">
        <v-card class="center" height="265px">
          <div id="chart">
            <apexchart type="donut" :options="chartOptionsEnergy" :series="seriesEnergy"></apexchart>
          </div>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>
<script>
export default {
  data() {
    return {
      result: "",
      machineName: "",
      machineID: "",
      department: "",
      seriesPower: [],
      seriesFrequency: [],
      seriesEnergy: [],
      resultcheck:"",
      chartOptionsPower: {
        chart: {
          width: 380,
          type: "donut",
          dropShadow: {
            enabled: false,
            color: "#111",
            top: -1,
            left: 3,
            blur: 3,
            opacity: 0.2
          }
        },
        stroke: {
          width: 0
        },
        plotOptions: {
          pie: {
            donut: {
              // labels: {
              //   show: true,
              //   total: {
              //     showAlways: true,
              //     show: true
              //   }
              // }
            }
          }
        },
        labels: ["p0" + "", "p1" + " -", "p2" + " "],

        states: {
          hover: {
            filter: "none"
          }
        },
        colors: ["#69F0AE", "#00E676", "#00C853"],
        title: {
          text: "Power factor"
        },
        dataLabels: {
          show: true,
          value: {
            show: true,
            fontSize: "14px",
            fontFamily: undefined,
            fontWeight: 400,
            color: undefined,
            offsetY: 16,
            formatter: function(val) {
              return val;
            }
          }
        },
        responsive: [
          {
            breakpoint: 480,
            options: {
              chart: {
                width: 200
              },
              legend: {
                position: "bottom"
              }
            }
          }
        ]
      },
      chartOptionsFrequency: {
        chart: {
          width: 380,
          type: "donut",
          dropShadow: {
            enabled: false,
            color: "#111",
            top: -1,
            left: 3,
            blur: 3,
            opacity: 0.2
          }
        },
        stroke: {
          width: 0
        },
        plotOptions: {
          pie: {
            donut: {
              // labels: {
              //   show: true,
              //   total: {
              //     showAlways: true,
              //     show: true
              //   }
              // }
            }
          }
        },
        labels: ["f0" + "", "f1" + " -", "f2" + " "],
        states: {
          hover: {
            filter: "none"
          }
        },
        colors: ["#BBDEFB", "#64B5F6", "#1E88E5"],
        title: {
          text: "Frequency"
        },
        responsive: [
          {
            breakpoint: 480,
            options: {
              chart: {
                width: 200
              },
              legend: {
                position: "bottom"
              }
            }
          }
        ]
      },
      chartOptionsEnergy: {
        chart: {
          width: 380,
          type: "donut",
          dropShadow: {
            enabled: false,
            color: "#111",
            top: -1,
            left: 3,
            blur: 3,
            opacity: 0.2
          }
        },
        stroke: {
          width: 0
        },
        plotOptions: {
          pie: {
            donut: {
              labels: {
                show: true,
                total: {
                  show: true,
                  label: "Total",
                  color: "#373d3f",
                  fontSize: "16px",
                  fontFamily: undefined,
                  fontWeight: 600,
                   formatter: function (w) {
                    return w.globals.seriesTotals.reduce((a, b) => {
                      return a + b
                    },0.01)
                  }
                }
              },
            }
          }
        },
        labels: ["e0" + "", "e1" + " -", "e2" + " "],
        states: {
          hover: {
            filter: "none"
          }
        },
        colors: ["#F8BBD0", "#F06292", "#D81B60"],
        title: {
          text: "Energy"
        },
        responsive: [
          {
            breakpoint: 480,
            options: {
              chart: {
                width: 200
              },
              legend: {
                position: "bottom"
              }
            }
          }
        ]
      }
    };
  },
  created() {
    this.getdata_storage();
    this.getdata_device();
  },
  methods: {
    getdata_device() {
      this.loading = true;
      this.axios({
        method: "GET",
        url: this.$store.state.url_axios + "api/v1/gauge/" + this.machineID,
        headers: { Authorization: "Bearer " + localStorage.token }
      }).then(
        response => {
          this.loading = false;
          this.resultcheck = response.data;
          this.result = response.data.result;
          // console.log();
          if (this.resultcheck["message"] === "success") {
            this.seriesPower.push(
              this.result.P0,
              this.result.P1,
              this.result.P2
            );
            this.seriesFrequency.push(
              this.result.f0,
              this.result.f1,
              this.result.f2
            );
            this.seriesEnergy.push(
              this.result.E0,
              this.result.E1,
              this.result.E2
            );
          } else {
            this.seriesPower = [0, 0, 0];
            this.seriesFrequency = [0, 0, 0];
            this.seriesEnergy = [0, 0, 0];
          }
        },
        response => {
          if (response.message === "Request failed with status code 400") {
            this.$swal("กรุณากรอกข้อมูลให้ครบถ้วน", "", "warning");
          } else if (
            response.message === "Request failed with status code 500"
          ) {
            this.$swal("เกิดข้อผิดพลาด กรุณาตรวจสอบการเชื่อมต่อ", "", "error");
          } else if (
            response.message === "Request failed with status code 401"
          ) {
            this.toPage("logout");
          } else if (
            response.message === "Request failed with status code 422"
          ) {
            this.toPage("logout");
          } else if (response.message === "Network Error") {
            this.$swal("กรุณาตรวจสอบการเชื่อมต่อ", "", "warning");
          }
        }
      );
    },
    getdata_storage() {
      let data = JSON.parse(window.localStorage.getItem("arrayDevice"));
      if (data === null) {
        this.array_getdata = [];
      } else {
        this.array_getdata = data;
      }
      this.machineName = this.array_getdata[0].machineName;
      this.machineID = this.array_getdata[0].machineID;
      this.department = this.array_getdata[0].department;
    }
  }
};
</script>
