<template>
  <div id="chart">
    <v-row align="center" justify="center" class>
      <v-col style="text-align: -webkit-center;">
        <v-card class="center" height="220px">
           <v-row>
                <v-col>
                  <v-container>
                    <v-list-item three-line>
                      <v-list-item-content>
                        <v-card color="pink">
                          <v-container>
                            <v-row class="truncate">
                              <v-col>
                                <v-list-item-title
                                  class="Subtitle 1 white--text font-weight-black"
                                >Current</v-list-item-title>
                              </v-col>
                            </v-row>
                          </v-container>
                        </v-card>
                      </v-list-item-content>
                    </v-list-item>
                    <v-list-item three-line class="mb-2">
                      <v-list-item-content>
                        <v-card color="purple accent-2">
                            <v-row class="truncate">
                              <v-col >
                                <v-list-item-title
                                  class="Subtitle 1 white--text font-weight-black"
                                ><v-icon color="white" class="mr-1">mdi-sigma</v-icon> Total = {{totalSeriesSemi}}</v-list-item-title>
                                 <v-list-item-title
                                  class="Subtitle 1 white--text font-weight-black"
                                ><v-icon color="white" class="mr-1">mdi-chart-arc</v-icon> AVG = {{avgSeriesSemi}}</v-list-item-title>
                              </v-col>
                            </v-row>
                        </v-card>
                      </v-list-item-content>
                    </v-list-item>
                  </v-container>
                </v-col>
              </v-row>

        </v-card>
      </v-col>
      <v-col style="text-align: -webkit-center;">
        <v-card>
          <div id="chart">
            <apexchart type="radialBar" :options="chartOptionsSemi1" :series="seriesSemi1"></apexchart>
          </div>
        </v-card>
      </v-col>
    </v-row>

    <v-row>
      <v-col style="text-align: -webkit-center;">
        <v-card>
          <div id="chart">
            <apexchart type="radialBar" :options="chartOptionsSemi2" :series="seriesSemi2"></apexchart>
          </div>
        </v-card>
      </v-col>

      <v-col style="text-align: -webkit-center;">
        <v-card>
          <div id="chart">
            <apexchart type="radialBar" :options="chartOptionsSemi3" :series="seriesSemi3"></apexchart>
          </div>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>
<script>
export default {
  data() {
    return {
      resultcheck: "",
      result: "",
      avgSeriesSemi: 0,
      totalSeriesSemi: 0,
      machineName: "",
      machineID: "",
      department: "",

      chartOptionsSemi1: {
        chart: {
          height: 250,
          type: "radialBar",
          offsetY: -5,
          sparkline: {
            enabled: true
          }
        },
        plotOptions: {
          radialBar: {
            startAngle: -90,
            endAngle: 90,
            track: {
              background: "#e7e7e7",
              strokeWidth: "100%",
              margin: 1, // margin is in pixels
              dropShadow: {
                enabled: true,
                top: 1,
                left: 0,
                color: "#999",
                opacity: 1,
                blur: 1
              }
            },
            dataLabels: {
              name: {
                fontSize: "18px",
                color: undefined,
                offsetY: 100
              },
              value: {
                show: true,
                fontSize: "22px",
                fontFamily: undefined,
                fontWeight: 400,
                color: undefined,
                offsetY: 16,
                formatter: function(val) {
                  return val + " " + "A";
                }
              }
            }
          }
        },
        grid: {
          padding: {
            top: -5
          }
        },
        fill: {
          type: "gradient",
          gradient: {
            shade: "light",
            shadeIntensity: 0.4,
            inverseColors: false,
            opacityFrom: 1,
            opacityTo: 1,
            stops: [0, 50, 53, 91]
          }
        },
        colors: ["#E040FB"],
        labels: ["Current  phase 1"]
      },
      chartOptionsSemi2: {
        chart: {
          height: 250,
          type: "radialBar",
          offsetY: -5,
          sparkline: {
            enabled: true
          }
        },
        plotOptions: {
          radialBar: {
            startAngle: -90,
            endAngle: 90,
            track: {
              background: "#e7e7e7",
              strokeWidth: "97%",
              margin: 1, // margin is in pixels
              dropShadow: {
                enabled: true,
                top: 1,
                left: 0,
                color: "#999",
                opacity: 1,
                blur: 1
              }
            },
            dataLabels: {
              name: {
                fontSize: "18px",
                color: undefined,
                offsetY: 100
              },
              value: {
                show: true,
                fontSize: "22px",
                fontFamily: undefined,
                fontWeight: 400,
                color: undefined,
                offsetY: 16,
                formatter: function(val) {
                  return val + " " + "A";
                }
              }
            }
          }
        },
        grid: {
          padding: {
            top: -5
          }
        },
        fill: {
          type: "gradient",
          gradient: {
            shade: "light",
            shadeIntensity: 0.4,
            inverseColors: false,
            opacityFrom: 1,
            opacityTo: 1,
            stops: [0, 50, 53, 91]
          }
        },
        colors: ["#F50057"],
        labels: ["Current  phase 2"]
      },
      chartOptionsSemi3: {
        chart: {
          height: 250,
          type: "radialBar",
          offsetY: -5,
          sparkline: {
            enabled: true
          }
        },
        plotOptions: {
          radialBar: {
            startAngle: -90,
            endAngle: 90,
            track: {
              background: "#e7e7e7",
              strokeWidth: "97%",
              margin: 1, // margin is in pixels
              dropShadow: {
                enabled: true,
                top: 1,
                left: 0,
                color: "#999",
                opacity: 1,
                blur: 1
              }
            },
            dataLabels: {
              name: {
                fontSize: "18px",
                color: undefined,
                offsetY: 100
              },
              value: {
                show: true,
                fontSize: "22px",
                fontFamily: undefined,
                fontWeight: 400,
                color: undefined,
                offsetY: 16,
                formatter: function(val) {
                  return val + " " + "A";
                }
              }
            }
          }
        },
        grid: {
          padding: {
            top: -5
          }
        },
        fill: {
          type: "gradient",
          gradient: {
            shade: "light",
            shadeIntensity: 0.4,
            inverseColors: false,
            opacityFrom: 1,
            opacityTo: 1,
            stops: [0, 150, 53, 91]
          }
        },
        colors: ["#A5D6A7"],
        labels: ["Current  phase 3"]
      },
      seriesSemi1: [],
      seriesSemi2: [],
      seriesSemi3: []
    };
  },
  computed: {},
  created() {
    this.getdata_storage();
    this.getdata_device();
  },
  methods: {
    getdata_device() {
      this.loading = true;
      this.axios({
        method: "GET",
        url: this.$store.state.url_axios + "api/v1/gauge/" + this.machineID,
        headers: { Authorization: "Bearer " + localStorage.token }
      }).then(response => {
        this.loading = false;

        this.resultcheck = response.data;
        this.result = response.data.result;

        if (this.resultcheck["message"] === "success") {
          this.seriesSemi1.push(this.result.I0 );
          this.seriesSemi2.push(this.result.I1 );
          this.seriesSemi3.push(this.result.I2 );

          this.avgSeriesSemi =
            (this.result.I0 + this.result.I1 + this.result.I2) / 3;
          this.totalSeriesSemi =
            this.result.I0 + this.result.I1 + this.result.I2;
        } else {
          this.seriesSemi1 = [0];
          this.seriesSemi2 = [0];
          this.seriesSemi3 = [0];
        }
      });
    },
    getdata_storage() {
      let data = JSON.parse(window.localStorage.getItem("arrayDevice"));
      if (data === null) {
        this.array_getdata = [];
      } else {
        this.array_getdata = data;
      }
      this.machineName = this.array_getdata[0].machineName;
      this.machineID = this.array_getdata[0].machineID;
      this.department = this.array_getdata[0].department;
    }
  }
};
</script>
<style>
.truncate {
  max-width: 200px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>